@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;400;700&display=swap");

:root {
	--font-light: 100;
	--font-regular: 400;
	--font-bold: 700;

	--al-teal: rgb(103, 198, 194);
	--al-new-bg: rgb(13, 14, 25);
	--off-white: rgb(242, 242, 242);
	--al-grey: rgb(51, 51, 51);
	--al-purple: rgb(34, 32, 80);
	--al-btn-bg: rgb(39, 40, 98);
	--al-btn-border: rgb(70, 70, 146);
	--al-btn-hover: rgb(116, 172, 232);
}

body {
	font-family: "Poppins", sans-serif;
	background-color: var(--al-new-bg);
	color: var(--off-white);
	width: 100vw;
	height: 100vh;
	font-size: 14px;
	letter-spacing: 0.04em;
	overflow-x: hidden;
}

.app {
	width: 100%;
	height: 100vh;
	display: flex;
	flex-direction: column;
	/* align-items: center; */
}

.app label {
	color: var(--off-white);
}

.app input {
	outline: none;
	padding: 0.5em 0 0.5em 1em;
	border: 1px solid var(--al-grey);
	background-color: var(--al-new-bg);
	color: var(--off-white);
}

.content_wrap {
	display: flex;
	flex-direction: column;
	padding-top: 4em;
	align-items: center;
}

.input_wrap {
	width: 50%;
	height: max-content;
	display: flex;
	flex-direction: column;
	background-color: rgb(17, 18, 32);
	padding: 1.8em;
	-webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
	box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
	margin-bottom: 4em;
	margin-left: 2.5em;
}

.input_wrap h1 {
	margin-bottom: 1em;
}

.game_select {
	display: flex;
	flex-direction: row;
	height: max-content;
	align-items: center;
	justify-content: center;
	gap: 3em;
}

.input_wrap .switch_icon {
	margin: 0 auto;
	font-size: 2em;
	cursor: pointer;
	transition: transform 0.5s ease-in-out;
}

.input_wrap .switch_icon:hover {
	transform: rotate(180deg);
}

.sens_input {
	display: flex;
	flex-direction: row;
	justify-content: center;
	gap: 3em;
}

.dpi_calc_wrap {
	display: flex;
	flex-direction: column;
	justify-content: center;
	margin-top: 2em;
}

.dpi_calc_input {
	display: flex;
	flex-direction: row;
	gap: 3em;
	margin-top: 0.6em;
}

/* hide input arrows on firefox */
input[type="number"] {
	-moz-appearance: textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
	display: none; /* <- Crashes Chrome on hover */
	-webkit-appearance: none;
	margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
}
